@import '../../styles/mixins';

.ServicesIconAndAbstract {
  width: 33%;
  padding: 38px 45px 20px 20px;
  @include flex(row, center, center);
  text-align: left;
  background-color: pink;

  .abstract-wrap {
    margin-left: 20px;

    h3 {
      text-transform: uppercase;
      font-weight: var(--bold);
      font-family: var(--header-font);
      font-size: var(--font-d-slide);
      line-height: 24px;
    }

    p {
      font-size: var(--font-d-btn);
      line-height: 20px;
      color: #333333;
      margin-top: 13px;
    }
  }
}
