@import '../../styles/mixins';

.home-page {
  &__splash,
  &__about,
  &__offer,
  &__call-to-action,
  &__services,
  /*  */
  &__references,
  &__contact,
  &__newsletter {
    position: relative;
    background-position: center center;
    overflow: hidden;
    padding-top: 50px;
    padding-bottom: 90px;
  }

  /* Splash */

  &__splash {
    background-image: url('../../images/splash.jpg');
    background-size: cover;
    background-attachment: fixed;
    z-index: 9;
    padding-bottom: 0;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.7);
      z-index: -9;
    }
  }

  /* About */

  &__about {
    @include flex(column, center, center);
  }

  /* Offer */

  &__offer {
    .offer-wrap {
      @include flex(row, flex-start, space-between);
      flex-wrap: wrap;
      padding: 0 20px;
    }
  }
  /* Call to action */
  &__call-to-action {
    background-color: var(--color-green-dark);
    padding: 55px 0;

    @include xl-max {
      padding: 0;
    }

    .call-to-action-wrap {
      @include flex(row, center, space-between);
      flex-wrap: wrap;
      position: relative;

      .headlings-wrap {
        color: var(--color-light);
        text-align: left;
        font-family: var(--header-font);
        margin-right: 30px;

        @include xl-max {
          padding: 55px 0;
          min-height: 288px;
        }

        h4 {
          font-size: var(--font-d-section-subtitle);
          line-height: 29px;
          font-weight: var(--normal);
          margin-bottom: 14px;

          @include sm-max {
            font-size: var(--font-m-subtitle);
          }
        }

        h2 {
          font-size: var(--font-d-section-header);
          line-height: 40px;
          font-weight: var(--normal);

          @include md-max {
            font-size: var(--font-d-section-subtitle);
            line-height: 26px;
          }
        }
      }

      button {
        margin: 30px 0 0 0px;

        @include xl-max {
          position: absolute;
          right: 0;
          bottom: 70px;
        }

        @include sm-max {
          bottom: 50px;
        }
      }
    }
  }

  /* Services */
  .services-icon-and-abstract-wrap {
    @include flex(row, center, space-between);
    flex-wrap: wrap;
  }

  &__contact {
    height: 35vh;
  }

  &__newsletter {
    background-color: var(--color-green-dark);

    .newsletter-section {
      @include flex(row, center, space-between);
      padding: 50px;

      @include md-max {
        @include flex(column-reverse, center, space-around);
      }
    }
  }
}
